import 'jest';
import { getResizedImageUrl } from '../ImageResizerHelper';
describe('ImageResizeHelperTest', () => {
    it('get resized url with only url input', () => {
        const result = getResizedImageUrl('https://cms-eastus-ppe-imageresizer-mr.cloudapp.net/cms/api/rushmore/imageFileData/MA7T7k');
        expect(result).toEqual('https://cms-eastus-ppe-imageresizer-mr.cloudapp.net/cms/api/rushmore/imageFileData/MA7T7k?m=6');
    });

    it('get resized url with width specified', () => {
        const result = getResizedImageUrl(
            'https://cms-eastus-ppe-imageresizer-mr.cloudapp.net/cms/api/rushmore/imageFileData/MA7T7k',
            1600
        );
        expect(result).toEqual('https://cms-eastus-ppe-imageresizer-mr.cloudapp.net/cms/api/rushmore/imageFileData/MA7T7k?w=1600&m=6');
    });

    it('get resized url with width and height specified', () => {
        const result = getResizedImageUrl(
            'https://cms-eastus-ppe-imageresizer-mr.cloudapp.net/cms/api/rushmore/imageFileData/MA7T7k',
            1600,
            600
        );
        expect(result).toEqual(
            'https://cms-eastus-ppe-imageresizer-mr.cloudapp.net/cms/api/rushmore/imageFileData/MA7T7k?w=1600&h=600&m=6'
        );
    });

    it('get resized url for non cms image', () => {
        const result = getResizedImageUrl(
            'https://cms-eastus-ppe-imageresizer-rt.cloudapp.net/cms/api/rushmore/imageFileData/MA7T7k',
            1600,
            600
        );
        expect(result).toEqual('https://cms-eastus-ppe-imageresizer-rt.cloudapp.net/cms/api/rushmore/imageFileData/MA7T7k');
    });
});
